

.home-card-container {
    width: 20rem;
    border-radius: 1rem;
    box-shadow: 0px 10px 8px #999;
    display: flex;
    flex-direction: column;
    margin: 0.5rem;
    height: 80%;
    background-color: white;
    flex-shrink: 0;
  }
  
  .home-card-img {
    width: 100%;
    border-radius: 1rem 1rem 0 0;
  }
  
  .home-card-title, .home-card-ticker {
    margin: 0.5rem 5%;
    align-self: center;
  }
  
  .home-card-description {
    margin: 0.5rem 5%;
    align-self: center;
  } 
  
